<template>
  <el-card>
    <template #header>
      <span>Hired Candidates</span>
      <span>
        <el-button type="primary" plain @click="dialogUpload = true">
          Bulk Upload</el-button
        >
        <el-button type="primary" plain @click="dialog = true"
          >Add Candidate</el-button
        >
      </span>
    </template>
    <el-dialog
      v-model="dialog"
      title="Add Candidate"
      :before-close="handleClose"
      width="65%"
    >
      <el-row>
        <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
          <el-steps :active="step" simple>
            <el-step title="Personal" />
            <el-step title="Employment" />
