          ></el-col>
          <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12">
            <el-form-item label="Date of Birth">
              <el-date-picker
                v-model="employee.date_of_birth"
                type="date"
                placeholder="Enter date of birth"
                style="width: 100%"
              />
            </el-form-item>
          </el-col>
          <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12">
            <el-form-item label="Gender">
              <el-select
                v-model="employee.gender"
                filterable
                placeholder="Select gender"
              >
                <el-option label="Male" value="male" />
                <el-option label="Female" value="female" />
                <el-option label="Other" value="other" />
              </el-select> </el-form-item
          ></el-col>
          <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12">
            <el-form-item label="Marital Status">
              <el-select
                v-model="employee.marital_status"
                filterable
                placeholder="Select marital status"
              >
                <el-option label="Single" value="single" />
                <el-option label="Married" value="married" />
                <el-option label="Divorced" value="divorced" />
                <el-option label="Window" value="window" />
                <el-option label="Windower" value="windower" />
              </el-select> </el-form-item
          ></el-col>
          <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12">
            <el-form-item
              label="Country"
              prop="country"
              :rules="[
                {
                  required: true,
                  message: 'Country is required',
                  trigger: ['blur'],
                },
              ]"
            >
              <el-select
                v-model="employee.country"
                filterable
                placeholder="Select country"
              >
                <el-option
                  v-for="country in $store.state.dictionary.countries"
