                  :value="country.name"
                />
              </el-select> </el-form-item
          ></el-col>
          <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12">
            <el-form-item
              label="Phone Number"
              prop="phone"
              :rules="[
                {
                  required: true,
                  message: 'Phone number is required',
                  trigger: ['blur'],
                },
              ]"
            >
              <el-input
                v-model="employee.phone"
                type="number"
                placeholder="Enter phone number"
              /> </el-form-item
          ></el-col>
          <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12">
            <el-form-item
              label="Email Address"
              prop="email"
              :rules="[
                {
                  required: true,
                  message: 'Email address is required',
                  trigger: ['blur'],
                },
                {
                  type: 'email',
                  message: 'Please enter valid address',
                  trigger: ['blur'],
                },
              ]"
            >
              <el-input
                v-model="employee.email"
                type="email"
                placeholder="Enter email address"
              /> </el-form-item
          ></el-col>
          <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12">
            <el-form-item label="KRA PIN">
              <el-input
                v-model="employee.kra_pin"
                placeholder="Enter kra pin"
              /> </el-form-item
          ></el-col>
          <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12">
            <el-form-item label="NHIF Number">
              <el-input
                v-model="employee.nhif_number"
                type="number"
                placeholder="Enter NHIF number"
              /> </el-form-item
          ></el-col>
          <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12">
            <el-form-item label="NSSF Number">
              <el-input
                v-model="employee.nssf_number"
                type="number"
                placeholder="Enter NSSF number"
              /> </el-form-item
          ></el-col>
        </el-row>

        <el-row v-else-if="step == 2">
          <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12">
            <el-form-item
              label="Job Title"
              prop="job_title_id"
              :rules="[
                {
                  required: true,
                  message: 'Job title is required',
                  trigger: ['blur'],
                },
              ]"
            >
              <el-select
                v-model="employee.job_title_id"
                filterable
                placeholder="Select job title"
              >
                <el-option
                  v-for="job_title in $store.state.company.job_titles"
                  :key="job_title"
                  :label="job_title.job_title"
                  :value="job_title.id"
                />
              </el-select> </el-form-item
          ></el-col>
          <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12">
            <el-form-item label="Employee Number" prop="employee_number">
              <el-input
                v-model="employee.employee_number"
                placeholder="Enter employee number"
              /> </el-form-item
          ></el-col>
          <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12">
            <el-form-item
              label="Employment Type"
              prop="employment_type_id"
              :rules="[
                {
                  required: true,
                  message: 'Employment type is required',
                  trigger: ['blur'],
                },
              ]"
            >
              <el-select
                v-model="employee.employment_type_id"
                filterable
                placeholder="Select employment type"
              >
                <el-option
                  v-for="employment_type in $store.state.company
                    .employment_types"
                  :key="employment_type"
                  :label="employment_type.employment_type"
                  :value="employment_type.id"
              /></el-select> </el-form-item
          ></el-col>
          <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12">
            <el-form-item
              prop="employment_start_date"
              label="Employment Start Date"
            >
              <el-date-picker
                v-model="employee.employment_start_date"
                type="date"
                placeholder="Enter employment start date"
                style="width: 100%"
              />
            </el-form-item>
          </el-col>
          <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12">
            <el-form-item label="Employer's Name">
              <el-select
                v-model="employee.work_station_id"
                filterable
                placeholder="Employer's Name"
              >
                <el-option
                  v-for="branch in $store.state.company.company_branches"
                  :key="branch"
                  :label="branch.company_name"
                  :value="branch.id"
                />
              </el-select> </el-form-item
          ></el-col>
          <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12">
            <el-form-item label="Department" prop="department_id">
              <el-select
                v-model="employee.department_id"
                filterable
                placeholder="Select department"
              >
                <el-option
                  v-for="department in $store.state.dictionary.departments"
                  :key="department"
                  :label="department.department"
                  :value="department.id"
                />
              </el-select> </el-form-item
          ></el-col>
        </el-row>
        <el-row v-else-if="step == 3">
          <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12">
            <el-form-item label="Basic Salary">
              <el-input
                v-model="employee.basic_salary"
                type="number"
                placeholder="Enter basic salary"
              />
            </el-form-item>
          </el-col>
          <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12">
            <el-form-item label="Bank Name" prop="bank">
              <el-select
                v-model="employee.bank"
                filterable
                placeholder="Select bank"
              >
                <el-option
                  v-for="bank in banks"
                  :key="bank.id"
                  :label="bank.bankname"
                  :value="bank"
                />
              </el-select> </el-form-item
          ></el-col>
          <el-col :xs="24" :sm="24" :md="12" :lg="12" :xl="12">
            <el-form-item label="Account Number" prop="account_number">
              <el-input
                v-model="employee.account_number"
                placeholder="Account number"
              />
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <template #footer>
        <span class="dialog-footer">
          <el-button v-if="step == 1" @click="handleClose">Cancel</el-button>
          <el-button v-else type="primary" plain @click="back()"
            >Back</el-button
          >
          <el-button
            v-if="step == 3"
            type="primary"
            plain
            @click="submit('employee')"
            :loading="loading"
            >Save</el-button
          >
          <el-button v-else type="primary" plain @click="next('employee')"
            >Next</el-button
          >
        </span>
      </template>
    </el-dialog>
    <el-dialog
      v-model="dialogUpload"
      title="Upload Bulk Employees"
      :before-close="handleClose"
    >
      <el-form
        :model="employee"
        size="large"
        ref="employee"
        label-position="top"
        :inline="true"
      >
        <el-row>
          <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
            <el-upload :auto-upload="false" :on-change="handleFileUpload" drag>
              <i class="fas fa-cloud el-icon--upload"></i>
              <div class="el-upload__text">
                Drop file here or <em>click to upload</em>
              </div>
              <template #tip>
                <div class="el-upload__tip">
                  <a
                    :href="
                      $helpers.BASE_URL +
                      `templates/batch-employees-template.xlsx`
                    "
                    target="_blank"
                  >
                    <span
                      ><i class="bx bx-download"></i>Click here to download
                      employee data template</span
                    >
                  </a>
                </div>
              </template>
            </el-upload>
            <el-progress
              v-if="loading"
              :percentage="50"
              status="warning"
              :indeterminate="true"
            />
          </el-col>
        </el-row>
      </el-form>
    </el-dialog>
    <el-tabs type="card" v-model="tab" @tab-click="filterElements">
      <el-tab-pane
        :label="`Hired (` + hired_count + `)`"
        name="Hired"
      ></el-tab-pane>
      <el-tab-pane
        :label="`On Probation (` + probation_count + `)`"
        name="Probation"
      ></el-tab-pane>
      <el-tab-pane
        :label="`On Contract (` + probation_count + `)`"
        name="Contract"
      ></el-tab-pane>
      <el-tab-pane
        :label="`On Notice Of Exit (` + notice_count + `)`"
        name="Notice"
      ></el-tab-pane>
      <el-tab-pane
        :label="`Exited (` + exited_count + `)`"
        name="Exited"
      ></el-tab-pane>
      <el-tab-pane
        :label="`Archived (` + archived_count + `)`"
        name="Archived"
      ></el-tab-pane>
      <el-tab-pane :label="`All (` + all_count + `)`" name="All"></el-tab-pane>
    </el-tabs>
    <el-table :data="employees" ref="elTable" v-loading="loading">
      <el-table-column prop="full_name" label="Employee" width="250">
        <template #default="scope">
          <router-link to="#" @click="edit(scope.row)">
            <div style="display: flex; align-items: center">
              <el-avatar
                v-if="scope.row.profile_photo"
                :src="scope.row.profile_photo"
              />
              <el-avatar v-else>
                {{
                  $helpers.getInitials(
                    scope.row.first_name + " " + scope.row.last_name
                  )
                }}
              </el-avatar>
              <span style="margin-left: 10px"
                ><span class="text-black">{{ scope.row.full_name }}<br /></span
                >{{ scope.row.employee_number }}</span
              >
            </div>
          </router-link>
        </template>
      </el-table-column>
      <el-table-column prop="job_title" label="Job Title" width="200">
        <template #default="scope">
          <span>{{ scope.row.job_title }}</span
          ><br />
          <span class="text-success">{{ scope.row.employment_type }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="work_station" label="Client" />
      <el-table-column
        prop="employment_start_date"
        label="Start Date"
        :formatter="$helpers.formatDate"
      />
      <el-table-column prop="status" label="Status" />
      <el-table-column prop="action" label="Action" width="70">
        <template #default="props">
          <el-dropdown>
            <i class="fas fa-ellipsis-v el-icon--right"></i>
            <template #dropdown>
              <el-dropdown-menu>
                <el-dropdown-item @click="edit(props.row)"
                  >Edit Candidate</el-dropdown-item
                >
                <el-dropdown-item @click="activate(props.row, 'Probation')"
                  >Set on Probation</el-dropdown-item
                >
                <el-dropdown-item @click="activate(props.row, 'Notice')"
                  >Set on Notice</el-dropdown-item
                >
                <el-dropdown-item @click="activate(props.row, 'Archived')"
                  >Archive</el-dropdown-item
                >
                <el-dropdown-item @click="activate(props.row, 'Exited')"
                  >Exit Candidate</el-dropdown-item
                >
              </el-dropdown-menu>
            </template>
          </el-dropdown>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      background
      layout="prev, pager, next"
      :total="employees ? employees.length : 0"
    />
  </el-card>
</template>
<script>
export default {
  data() {
    return {
      filter: "",
      dialog: false,
      pay_element_dialog: false,
      listview: true,
      dialogUpload: false,
      loading: false,
      step: 1,
      tab: "Hired",
      hired_count: 0,
      notice_count: 0,
      probation_count: 0,
      leave_count: 0,
      exited_count: 0,
      archived_count: 0,
      all_count: 0,
      employee: {
        country: "Kenya",
        status: "Active",
        date_of_birth: new Date(
          new Date().setFullYear(new Date().getFullYear() - 18)
        ),
        employment_start_date: new Date(),
      },
      file: "",
    };
  },
  computed: {
    banks() {
      if (this.$store.state.dictionary.banks != undefined) {
        var banks = this.$store.state.dictionary.banks.filter(function (bank) {
          return bank.category == "BANK";
        });
        return banks;
      }
    },
    employees() {
      var tab = this.tab;
      this.loading = true;
      if (this.$store.state.dictionary.employees != undefined) {
        var hired = this.$store.state.dictionary.employees.filter(function (
          row
        ) {
          return row.status == "Hired";
        });
        var notice = this.$store.state.dictionary.employees.filter(function (
          row
        ) {
          return row.status == "Notice";
        });
        var probation = this.$store.state.dictionary.employees.filter(function (
          row
        ) {
          return row.status == "Probation";
        });
        var exited = this.$store.state.dictionary.employees.filter(function (
          row
        ) {
          return row.status == "Exited";
        });
        var archived = this.$store.state.dictionary.employees.filter(function (
          row
        ) {
          return row.status == "Archived";
        });
        this.hired_count = hired.length;
        this.notice_count = notice.length;
        this.probation_count = probation.length;
        this.exited_count = exited.length;
        this.archived_count = archived.length;
        this.all_count = this.$store.state.dictionary.employees.length;

        if (this.tab == "All") {
          this.loading = false;
          return this.$store.state.dictionary.employees;
        } else {
          var filtered = this.$store.state.dictionary.employees.filter(
            function (row) {
              return row.status == tab;
            }
          );
          this.loading = false;
          return filtered;
        }
      }
    },
  },
  mounted() {
    this.$store.dispatch("dictionary/getDepartments");
    this.$store.dispatch("dictionary/getCountries");
    this.$store.dispatch("dictionary/getEmployees");
    this.$store.dispatch("company/getCompanyBranches");
    this.$store.dispatch("company/getJobTitles");
    this.$store.dispatch("company/getEmploymentTypes");
  },
  methods: {
    next(validate) {
      this.$refs[validate].validate((valid) => {
        if (valid) {
          this.step++;
        } else {
          return false;
        }
      });
    },
    back() {
      this.step--;
    },
    filterElements(tab) {
      this.tab = tab.props.name;
    },
    submit(validate) {
      this.$refs[validate].validate((valid) => {
        if (valid) {
          this.step = 3;
          this.loading = true;
          let data = this.employee;
          data.company_id = this.$store.state.auth.user.company_id;
          this.$endpoint.savePersonnel(data).then(
            (res) => {
              this.loading = false;
              this.$store.dispatch("dictionary/getEmployees");
              this.step = 1;
              this.employee = {};
              this.employee = {
                country: "Kenya",
                status: "Active",
                date_of_birth: new Date(
                  new Date().setFullYear(new Date().getFullYear() - 18)
                ),
                employment_start_date: new Date(),
              };
              this.dialog = false;
              this.$notify({
                title: "Success",
                message: res.data.message,
                type: "success",
              });
            },
            (error) => {
              this.loading = false;
              this.message =
                (error.response &&
                  error.response.data &&
                  error.response.data.message) ||
                error.message ||
                error.toString();
              this.$notify({
                title: "Error",
                message: this.message,
                type: "error",
              });
            }
          );
        } else {
          return false;
        }
      });
    },
    edit(data) {
      this.employee = data;
      this.dialog = true;
    },
    activate(data, status) {
      this.employee = data;
      this.employee.status = status;
      this.$endpoint.savePersonnel(this.employee).then(
        (res) => {
          this.loading = false;
          this.$store.dispatch("dictionary/getEmployees");
          this.step = 1;
          this.employee = {};
          this.employee = {
            country: "Kenya",
            status: "Active",
            date_of_birth: new Date(
              new Date().setFullYear(new Date().getFullYear() - 18)
            ),
            employment_start_date: new Date(),
          };
          this.dialog = false;
          this.$notify({
            title: "Success",
            message: res.data.message,
            type: "success",
          });
        },
        (error) => {
          this.loading = false;
          this.message =
            (error.response &&
              error.response.data &&
              error.response.data.message) ||
            error.message ||
            error.toString();
          this.$notify({
            title: "Error",
            message: this.message,
            type: "error",
          });
        }
      );
    },
    deletePersonnel(id) {
      if (confirm("Do you really want to delete this record?")) {
        this.$endpoint.deletePersonnel(id).then(
          (res) => {
            this.$store.dispatch("dictionary/getEmployees");
            this.$notify({
              title: "Success",
              message: res.data.message,
              type: "success",
            });
          },
          (error) => {
            this.loading = false;
            this.message =
              (error.response &&
                error.response.data &&
                error.response.data.message) ||
              error.message ||
              error.toString();
            this.$notify({
              title: "Error",
              message: this.message,
              type: "error",
            });
          }
        );
      }
    },
    handleFileUpload(e) {
      this.loading = true;
      this.file = e.raw;
      let formData = new FormData();
      formData.append("file", this.file);
      this.$endpoint
        .batchEmployees(this.$store.state.auth.user.company_id, formData)
        .then(
          (res) => {
            this.$store.dispatch("dictionary/getEmployees");
            this.loading = false;
            this.dialogUpload = false;
            this.$notify({
              title: "Success",
              message: res.data.message,
              type: "success",
            });
          },
          (error) => {
            this.loading = false;
            this.message =
              (error.response &&
                error.response.data &&
                error.response.data.message) ||
              error.message ||
              error.toString();
            this.$notify({
              title: "Error",
              message: this.message,
              type: "error",
            });
          }
        );
    },
    handleClose() {
      this.employee = {};
      this.dialog = false;
      this.dialogUpload = false;
    },
  },
};
</script>
